
// Прелоадер
document.addEventListener("DOMContentLoaded", () => {
    const lockElements = document.querySelectorAll('.lock');
    const preloaderActiveElement = document.querySelector('.preloader__active');
    const preloaderCircleElement = document.querySelector('.preloader__circle');
  
    preloaderCircleElement.addEventListener('click', () => {
      lockElements.forEach(element => {
        element.style.transition = "opacity 0.5s";
        element.style.opacity = "0";
        setTimeout(() => {
          element.classList.remove('lock');
          element.style.opacity = "1";
          element.style.transition = "opacity 0.5s";
        }, 0);
      });
  
      preloaderActiveElement.style.transition = "opacity 0.5s";
      preloaderActiveElement.style.opacity = "0";
      setTimeout(() => {
        preloaderActiveElement.classList.remove('preloader__active');
        preloaderActiveElement.style.opacity = "1";
        preloaderActiveElement.style.transition = "opacity 0.5s";
      }, 1000);
      setTimeout(() => {
        preloaderActiveElement.style.display = "none";
        preloaderActiveElement.style.transition = "display 0.5s";
      }, 1000);
    });
  });


// Переход на предыдущую страницу
document.addEventListener("DOMContentLoaded", function(){
  const arrowLink = document.querySelector('.head__link');

  arrowLink.addEventListener('click', function(event) {
    event.preventDefault(); // Отменяем стандартное поведение ссылки

    const previousPage = document.referrer; // Получаем URL предыдущей страницы

    if (previousPage) {
      // Если есть предыдущая страница, переходим на нее
      window.location.href = previousPage;
    } else {
      // Если предыдущей страницы нет - переходим на главную
      window.location.href = '/';
    }
  });
});


// отправка формы на почту
document.addEventListener('DOMContentLoaded', function() {
  var button = document.getElementById('button-send');
  button.addEventListener('click', function(e) {
    e.preventDefault();
    
    // Получаем значения из формы
    var name = document.getElementById('name').value;
    var phone = document.getElementById('phone').value;
    var checkboxes = [];
    var inputs = document.querySelectorAll('input[type=checkbox]:checked');
    for (var i = 0; i < inputs.length; i++) {
      checkboxes.push(inputs[i].value);
    }
    var textarea = document.getElementById('textarea').value;
    
    // Создаем объект XMLHttpRequest
    var xhr = new XMLHttpRequest();
    var url = 'submit_form.php';
    
    // Открываем соединение с сервером
    xhr.open('POST', url, true);
    
    // Устанавливаем заголовок нужного типа контента
    xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
    
    // Отправляем данные на сервер
    xhr.send('name=' + encodeURIComponent(name) + '&phone=' + encodeURIComponent(phone) + '&checkboxes=' + encodeURIComponent(checkboxes.join(',')) + '&textarea=' + encodeURIComponent(textarea));
    
    // Обработка ответа от сервера
    xhr.onreadystatechange = function() {
      if (xhr.readyState === 4 && xhr.status === 200) {
        // Выводим ответ сервера
        alert(xhr.responseText);
      }
    };
  });
});


// скопировать ссылку
document.getElementById("copi").addEventListener("click", function() {
  var elementToCopy = document.getElementsByClassName("earn-play-to-earn__link")[0];
  var textToCopy = elementToCopy.innerText;
  
  navigator.clipboard.writeText(textToCopy)
    .then(function() {
      alert("Ссылка скопирована успешно");
    })
    .catch(function(error) {
      alert("Ссылка не была скопирована");
    });
});